import fetchCountries from "./fetchCountries";

const input = document.querySelector("#search-box");
const listEl = document.querySelector("#country-list");
const infoEl = document.querySelector("#country-info");

input.addEventListener("input", debounce(onSearch, 500));

function onSearch(e) {
  const name = e.target.value.trim();
  if (!name) {
    clearResults();
    return;
  }

  fetchCountries(name)
    .then((countries) => {
      clearResults();

      if (countries.length > 10) {
        info({
          text: "Too many matches. Please be more specific.",
        });
      } else if (countries.length >= 2 && countries.length <= 10) {
        renderCountryList(countries);
      } else if (countries.length === 1) {
        renderCountryInfo(countries[0]);
      }
    })
    .catch(() => {
      error({ text: "No countries found!" });
      clearResults();
    });
}

function clearResults() {
  listEl.innerHTML = "";
  infoEl.innerHTML = "";
}

function renderCountryInfo(country) {
  const { name, capital, population, languages, flags } = country;
  const langs = languages.map((lang) => `<li>${lang.name}</li>`).join("");
  const markup = `
    <h2>${name}</h2>
    <p><b>Capital:</b> ${capital}</p>
    <p><b>Population:</b> ${population.toLocaleString()}</p>
    <p><b>Languages:</b></p>
    <ul>${langs}</ul>
    <img src="${flags.svg}" width="150" />
  `;
  infoEl.innerHTML = markup;
}
